const jobs = (state = [], action) => {
	switch (action.type) {
		case "ADD_JOB":
			return [
				...state,
				{
					id: action.id,
					text: action.text,
					completed: false
				}
			];
		case "TOGGLE_JOB":
			return state.map(job => (job.id === action.id ? { ...job, completed: !job.completed } : job));
		default:
			return state;
	}
};

export default jobs;



